.header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    position:sticky;
    top: 0px;
    z-index: 100;
    background-color: white;
    /* border-bottom: 0.0125px solid black; */
}
.header__logo{
    height: 25px;
    object-fit: contain;
    margin-left: 20px;
}
.header__input{
    display: flex;
    align-items: center;
    height: 35px;
    width: 40%;
    border: 1px solid black;
    border-radius: 9px;
}
.header__icons{
    display: flex;
    flex-wrap: wrap;
    gap: 25px;
}
.header__input >input{
    flex: 1;
    border: none;
}
.header__inputButton{
    width: 50px !important;
    background-color: #fafafa;
    border-left: 1px solid lightgray;
    color: gray;
    border-radius: 9px;
}
.header__icon{
    margin-right: 8px;
}