
.videoCard {
    background-color: #ffffff;
    border: 1px solid #e0e0e0;
    border-radius: 8px;
    
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
    cursor: pointer;
    /* overflow: hidden; */
    /* transition: transform 0.2s, box-shadow 0.2s; */
  }
  
  .videoCard:hover {
    transform: scale(1);
    box-shadow: 0 4px 12px rgba(255, 255, 255, 0.15);
  }
  
  .videoCard__thumbnail {
    position: relative;
  }
  
  .videoCard__thumbnail img {
    width: 100%;
    height: auto;
    display: block;
  }
  
  .videoCard__liveBadge {
    position: absolute;
    top: 8px;
    left: 8px;
    background-color: #ff0000;
    color: #ffffff;
    padding: 2px 6px;
    border-radius: 12px;
    font-size: 12px;
    font-weight: bold;
  }
  
  .videoCard__info {
    padding: 10px;
  }
  
  .videoCard__title {
    font-size: 16px;
    font-weight: bold;
    margin: 0;
    color: #333;
  }
  
  .videoCard__author {
    font-size: 14px;
    color: #555;
    margin: 5px 0;
  }
  
  .videoCard__views, .videoCard__uploadTime {
    font-size: 12px;
    color: #777;
    margin: 2px 0;
  }
  