.library {
    padding: 20px;
    background-color: #f9f9f9;
  }
  
  .library__content {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  
  .library__section {
    background-color: #ffffff;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  }
  
  .library__section h3 {
    font-size: 18px;
    margin-bottom: 10px;
  }
  
  .library__section p {
    color: #606060;
    font-size: 14px;
  }
  